import { Injectable } from '@angular/core';
import { BehaviorSubject } from "rxjs"

@Injectable({
  providedIn: 'root'
})
export class UsersService {

  compListing = [{
    user : "Manisha"
  },
  {
    user : "Mansi"
  },
  {
    user : "Rajat"
  },
  {
    user : "Sunita"
  }];

  listing = new BehaviorSubject(this.compListing);

  constructor() {  }

  updateData()
  {
    this.listing.next([])
  }

  getData()
  {
    return this.listing;
  }

  changeName(name)
  {
    this.listing.next([{
      user:name
    }])
  }
}
